html, body {
  width: 100%;
  height: 100%;
  background: #FFF2E1;
}

#hotdoglogo{
  text-align: center;
  background: #44182F;
  position: relative;
  top: 0px;
  margin: 0px;
  padding: 25px;
  }
  h2 {
    text-align: center;
    position: static;
    font-size: 3em;
    padding: 15px;
    top: 0px;
    color: #C83233;
    width: 100%;
    border: none;
    outline: none;
      }
 
.myForm {
  text-align: center;
  margin: 0 auto;
  width: 100%;
	padding: 30px;
	color: #44182F;
  position: relative;
  background: #F79B14;
  overflow: hidden;
}

.formField{
  display: block;	
  margin: auto;
	width: 200px;	
	border: none;
	border-radius: 20px;
	outline: none;
	padding: 10px;
	font-size: 0,5em;
	color: #676767;
	transition: border 0.5s;
	-webkit-transition: border 0.5s;
	-moz-transition: border 0.5s;
	-o-transition: border 0.5s;
	border: solid 3px #C83233;	
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
	box-sizing:border-box;
	}
  .myButton{
    text-align: center;
    width: 100%;
    background-color: #44182F;
    color: white;   
    cursor: pointer;
    display: block;	
    margin: auto;
    width: 200px;	
    border: none;
    border-radius: 20px;
    outline: none;
    padding: 10px;
    font-size: 0,5em;
    transition: border 0.5s;
    -webkit-transition: border 0.5s;
    -moz-transition: border 0.5s;
    -o-transition: border 0.5s;
    border: solid 3px #C83233;	
    -webkit-box-sizing:border-box;
    -moz-box-sizing:border-box;
    box-sizing:border-box;
  }
  
  .myList{
    padding: 5px;
    font-size: 18px;
    margin-left: 10px;
    margin-top: 5px;
    color: #C83233;
    border: none;
    outline: none;
  }
  
  .myListButton{
    display: inline-block;
    margin: 10px;
    height: auto;
    width: 100px;
    background-color: #44182F;
    color: white;   
    border-radius: 5px;
    border: solid 3px #C83233;	
    outline: none;
    padding: 5px;
     }
